import { _decorator, Component, Sprite, tween, Vec2 } from 'cc';
import { BetZoneComponent } from './BetZoneComponent';
const { ccclass, property } = _decorator;

@ccclass('RadialTimer')
export class RadialTimer extends Component {
  @property(Sprite)
  timerSprite: Sprite = null; // Drag the Sprite node to this property in the Inspector

  @property
  duration: number = 1; // Duration of the timer in seconds

  start() {
    this.startTimer();
  }

  startTimer() {
    // Set the initial fillRange to 1 (full circle)
    this.timerSprite.fillRange = 1;

    // Animate fillRange from 1 (full) to 0 (empty)
    tween(this.timerSprite)
      .to(this.duration, { fillRange: 0 }, { easing: 'linear' }) // Linear by default
      .call(() => {
        console.log('Timer completed!');
        this.onTimerComplete();
      })
      .start();
  }

  onTimerComplete() {
    this.clearChipsInAllZones();
    // Add logic when the timer completes
  }
}
